import React from "react"
import PropTypes from "prop-types"
import { FaArrowLeft } from "react-icons/fa"
import { Link } from "gatsby"

const Header = ({ title = "" }) => {
  return (
    <>
      <header className="py-3 bg-primary-500 shadow fixed top-0 left-0 right-0 z-10">
        <nav className="size__container flex justify-between items-center text-white text-xl">
          <div className="px-2 text-3xl">
            <Link to="/" replace>
              <FaArrowLeft />
            </Link>
          </div>

          <h1>{title}</h1>
          <span></span>
        </nav>
      </header>
      <div className="h-20"></div>
    </>
  )
}

Header.propTypes = {
  title: PropTypes.string,
}

export default Header
